/* Styles généraux */
.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

/* Conteneur du quiz */
.quiz-container {
  width: 100%;
  max-width: 500px;
  margin: 20px auto;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 8px;
  background-color: #333;
}

.timer-container {
  width: 100%;
  height: 20px;
  background-color: #444;
  border-radius: 10px;
  margin-bottom: 20px;
  position: relative;
}

.timer-bar {
  height: 100%;
  background-color: #0056b3;
  border-radius: 10px;
}

.options-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.option-button {
  background-color: #0056b3;
  color: white;
  border: none;
  padding: 15px;
  font-size: 1.2rem;
  cursor: pointer;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.option-button:hover {
  background-color: #004085;
}

.correct {
  background-color: #28a745;
}

.incorrect {
  background-color: #dc3545;
}

.quiz-finish {
  margin-top: 20px;
}

.restart-button {
  background-color: #0056b3;
  color: white;
  border: none;
  padding: 15px 30px;
  font-size: 1.2rem;
  cursor: pointer;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.restart-button:hover {
  background-color: #004085;
}

/* Styles spécifiques pour les petits écrans (mobile, tablette) */
@media (max-width: 768px) {
  .App-header {
    font-size: 18px;
  }

  .quiz-container {
    max-width: 90%;
    padding: 15px;
  }

  h1 {
    font-size: 2.5rem;
  }

  h2 {
    font-size: 1.8rem;
  }

  h3 {
    font-size: 1.2rem;
  }

  .option-button {
    font-size: 1.1rem;
    padding: 12px;
  }

  .restart-button {
    font-size: 1.1rem;
    padding: 12px 25px;
  }

  .timer-container {
    height: 15px;
  }

  .timer-bar {
    border-radius: 8px;
  }

  .quiz-finish {
    padding: 10px;
  }
}

/* Styles pour les très petits écrans (smartphones) */
@media (max-width: 480px) {
  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.6rem;
  }

  h3 {
    font-size: 1rem;
  }

  .quiz-container {
    padding: 10px;
  }

  .option-button {
    font-size: 1rem;
    padding: 10px;
  }

  .restart-button {
    font-size: 1rem;
    padding: 10px 20px;
  }

  .timer-container {
    height: 12px;
  }

  .timer-bar {
    border-radius: 5px;
  }
}

/* Animation de transition pour les éléments */
button:focus {
  outline: none;
}

.question-container {
  padding-top: 35px;
}

/* Feedback de la réponse */
.answer-feedback {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: -320px;
  padding: 10px;
  border-radius: 5px;
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
  color: white;
  z-index: 10;
}

/* Styles spécifiques pour les petits écrans (mobile, tablette) */
@media (max-width: 768px) {
  .answer-feedback {
    margin-top: -250px; /* Ajuste la position verticale sur les tablettes */
    font-size: 1.2rem; /* Réduit la taille de la police */
    padding: 8px;
    width: 80%; /* Permet de gérer la largeur sur les écrans plus petits */
  }
}

/* Styles pour les très petits écrans (smartphones) */
@media (max-width: 480px) {
  .answer-feedback {
    margin-top: -150px; /* Ajuste encore plus la position verticale sur mobile */
    font-size: 1rem; /* Réduit la taille de la police */
    padding: 6px;
    width: 90%; /* Largeur adaptée aux écrans mobiles */
  }
}

  